/* Reset and Base Styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
    line-height: 1.6;
    color: #ffffff;
    background: #000000;
    min-height: 100vh;
    overflow-x: hidden;
}

/* Homepage Container */
.homepage-container {
    min-height: 100vh;
    position: relative;
    overflow-x: hidden;
}

/* Hero Section */
.hero-section {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background: radial-gradient(ellipse at center, #1a1a2e 0%, #16213e 50%, #000000 100%);
    color: white;
    text-align: center;
    padding: 2rem;
    overflow: hidden;
}

.hero-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: 
        radial-gradient(circle at 20% 80%, rgba(120, 119, 198, 0.3) 0%, transparent 50%),
        radial-gradient(circle at 80% 20%, rgba(255, 119, 198, 0.3) 0%, transparent 50%),
        radial-gradient(circle at 40% 40%, rgba(120, 219, 255, 0.2) 0%, transparent 50%);
    animation: backgroundShift 20s ease-in-out infinite;
}

.hero-section.fade-in {
    animation: fadeInUp 1s ease-out;
}

.hero-content {
    max-width: 800px;
    z-index: 2;
    position: relative;
}

.profile-image {
    margin-bottom: 2rem;
}

.image-container {
    width: 180px;
    height: 180px;
    border-radius: 50%;
    background: linear-gradient(45deg, #00d4ff, #ff00ff, #00ff88);
    background-size: 300% 300%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    box-shadow: 
        0 0 30px rgba(0, 212, 255, 0.5),
        0 0 60px rgba(255, 0, 255, 0.3),
        0 0 90px rgba(0, 255, 136, 0.2);
    animation: float 3s ease-in-out infinite, gradientShift 3s ease-in-out infinite;
    position: relative;
    border: 3px solid rgba(255, 255, 255, 0.2);
    padding: 4px;
    overflow: hidden;
}

.image-container::before {
    content: '';
    position: absolute;
    top: -3px;
    left: -3px;
    right: -3px;
    bottom: -3px;
    border-radius: 50%;
    background: linear-gradient(45deg, #00d4ff, #ff00ff, #00ff88);
    background-size: 300% 300%;
    z-index: -1;
    animation: gradientShift 3s ease-in-out infinite;
    filter: blur(10px);
    opacity: 0.7;
}

.profile-picture {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
    z-index: 2;
    position: relative;
}


.initials {
    font-size: 3.5rem;
    font-weight: 900;
    background: linear-gradient(45deg, #ffffff, #00d4ff, #ff00ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0 0 30px rgba(0, 212, 255, 0.5);
    animation: textGlow 2s ease-in-out infinite alternate;
}

.hero-title {
    font-size: 3.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    line-height: 1.2;
}

.highlight {
    background: linear-gradient(45deg, #00d4ff, #ff00ff, #00ff88);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0 0 20px rgba(0, 212, 255, 0.5);
    animation: textPulse 2s ease-in-out infinite;
}

.hero-subtitle {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    opacity: 0.9;
    font-weight: 300;
}

.hero-description {
    font-size: 1.1rem;
    margin-bottom: 2.5rem;
    opacity: 0.8;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.hero-buttons {
    display: flex;
    gap: 1rem;
    justify-content: center;
    flex-wrap: wrap;
}

.btn-primary, .btn-secondary {
    padding: 12px 30px;
    border: none;
    border-radius: 50px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    text-decoration: none;
    display: inline-block;
}

.btn-primary {
    background: linear-gradient(45deg, #00d4ff, #ff00ff);
    color: white;
    box-shadow: 
        0 0 20px rgba(0, 212, 255, 0.4),
        0 0 40px rgba(255, 0, 255, 0.2);
    border: 1px solid rgba(0, 212, 255, 0.3);
}

.btn-primary:hover {
    transform: translateY(-3px);
    box-shadow: 
        0 0 30px rgba(0, 212, 255, 0.6),
        0 0 60px rgba(255, 0, 255, 0.4);
    background: linear-gradient(45deg, #00e5ff, #ff0aff);
}

.btn-secondary {
    background: transparent;
    color: white;
    border: 2px solid rgba(0, 212, 255, 0.5);
    box-shadow: 0 0 10px rgba(0, 212, 255, 0.2);
}

.btn-secondary:hover {
    background: linear-gradient(45deg, rgba(0, 212, 255, 0.1), rgba(255, 0, 255, 0.1));
    color: #00d4ff;
    transform: translateY(-3px);
    box-shadow: 0 0 20px rgba(0, 212, 255, 0.4);
    border-color: #00d4ff;
}

/* Floating Shapes */
.hero-decoration {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 1;
}

.floating-shape {
    position: absolute;
    border-radius: 50%;
    background: linear-gradient(45deg, rgba(0, 212, 255, 0.3), rgba(255, 0, 255, 0.3));
    animation: float 6s ease-in-out infinite;
    box-shadow: 0 0 20px rgba(0, 212, 255, 0.3);
}

.shape-1 {
    width: 80px;
    height: 80px;
    top: 20%;
    left: 10%;
    animation-delay: 0s;
}

.shape-2 {
    width: 120px;
    height: 120px;
    top: 60%;
    right: 10%;
    animation-delay: 2s;
}

.shape-3 {
    width: 60px;
    height: 60px;
    top: 80%;
    left: 20%;
    animation-delay: 4s;
}

/* Navigation */
.main-nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.9);
    backdrop-filter: blur(20px);
    z-index: 1000;
    padding: 1rem 0;
    box-shadow: 0 2px 20px rgba(0, 212, 255, 0.1);
    border-bottom: 1px solid rgba(0, 212, 255, 0.2);
}

.nav-container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2rem;
}

.nav-logo {
    font-size: 1.8rem;
    font-weight: 900;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: logoGlow 3s ease-in-out infinite;
}

.nav-image-container {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: linear-gradient(45deg, #00d4ff, #ff00ff, #00ff88);
    background-size: 300% 300%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 
        0 0 15px rgba(0, 212, 255, 0.5),
        0 0 30px rgba(255, 0, 255, 0.3);
    animation: gradientShift 3s ease-in-out infinite;
    position: relative;
    border: 2px solid rgba(255, 255, 255, 0.2);
    padding: 2px;
    overflow: hidden;
}

.nav-image-container::before {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    border-radius: 50%;
    background: linear-gradient(45deg, #00d4ff, #ff00ff, #00ff88);
    background-size: 300% 300%;
    z-index: -1;
    animation: gradientShift 3s ease-in-out infinite;
    filter: blur(5px);
    opacity: 0.7;
}

.nav-picture {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
    z-index: 2;
    position: relative;
    animation: navFloat 2s ease-in-out infinite;
}

@keyframes navFloat {
    0%, 100% {
        transform: translateY(0px) scale(1);
    }
    50% {
        transform: translateY(-2px) scale(1.05);
    }
}

.nav-links {
    display: flex;
    list-style: none;
    gap: 2rem;
}

.nav-link {
    background: none;
    border: none;
    color: #ffffff;
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
    padding: 0.5rem 1rem;
    border-radius: 25px;
    transition: all 0.3s ease;
    position: relative;
}

.nav-link:hover,
.nav-link.active {
    background: linear-gradient(45deg, #00d4ff, #ff00ff);
    color: white;
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(0, 212, 255, 0.3);
}

/* Main Content */
.main-content {
    background: #000000;
    position: relative;
    z-index: 2;
}

.content-section {
    padding: 5rem 2rem;
    max-width: 1200px;
    margin: 0 auto;
    opacity: 1;
    transform: translateY(0);
    transition: all 0.6s ease;
}

.content-section.active {
    opacity: 1;
    transform: translateY(0);
}

.section-header {
    text-align: center;
    margin-bottom: 3rem;
}

.section-title {
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    background: linear-gradient(45deg, #00d4ff, #ff00ff, #00ff88);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0 0 20px rgba(0, 212, 255, 0.3);
}

.section-divider {
    width: 80px;
    height: 4px;
    background: linear-gradient(45deg, #00d4ff, #ff00ff, #00ff88);
    margin: 0 auto;
    border-radius: 2px;
    box-shadow: 0 0 10px rgba(0, 212, 255, 0.5);
}

/* Skills Grid */
.skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 2rem;
    margin-top: 2rem;
}

.skill-card {
    background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
    padding: 2rem;
    border-radius: 15px;
    text-align: center;
    box-shadow: 
        0 10px 30px rgba(0, 0, 0, 0.3),
        0 0 20px rgba(0, 212, 255, 0.1);
    transition: all 0.3s ease;
    border: 1px solid rgba(0, 212, 255, 0.2);
    position: relative;
    overflow: hidden;
}

.skill-card:hover {
    transform: translateY(-10px);
    box-shadow: 
        0 20px 40px rgba(0, 0, 0, 0.4),
        0 0 30px rgba(0, 212, 255, 0.3);
    border-color: rgba(0, 212, 255, 0.5);
}

.skill-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(0, 212, 255, 0.1), transparent);
    transition: left 0.5s ease;
}

.skill-card:hover::before {
    left: 100%;
}

.skill-icon {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.skill-name {
    font-weight: 600;
    color: #ffffff;
    text-shadow: 0 0 10px rgba(0, 212, 255, 0.3);
}

/* Projects Grid */
.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2rem;
    margin-top: 2rem;
}

.project-card {
    background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 
        0 10px 30px rgba(0, 0, 0, 0.3),
        0 0 20px rgba(0, 212, 255, 0.1);
    transition: all 0.3s ease;
    border: 1px solid rgba(0, 212, 255, 0.2);
    position: relative;
}

.project-card:hover {
    transform: translateY(-10px);
    box-shadow: 
        0 20px 40px rgba(0, 0, 0, 0.4),
        0 0 30px rgba(0, 212, 255, 0.3);
    border-color: rgba(0, 212, 255, 0.5);
}

.project-image {
    height: 200px;
    background: linear-gradient(45deg, #00d4ff, #ff00ff, #00ff88);
    background-size: 300% 300%;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: gradientShift 3s ease-in-out infinite;
    position: relative;
    overflow: hidden;
}

.project-image::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg, rgba(0, 212, 255, 0.3), rgba(255, 0, 255, 0.3), rgba(0, 255, 136, 0.3));
    animation: gradientShift 3s ease-in-out infinite;
}

.project-placeholder {
    font-size: 4rem;
    opacity: 0.9;
    z-index: 2;
    position: relative;
    filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.3));
}

.project-content {
    padding: 2rem;
}

.project-title {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    color: #ffffff;
    text-shadow: 0 0 10px rgba(0, 212, 255, 0.3);
}

.project-description {
    color: #cccccc;
    margin-bottom: 1.5rem;
    line-height: 1.6;
}

.project-tech {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 1.5rem;
}

.tech-tag {
    background: linear-gradient(45deg, #00d4ff, #ff00ff);
    color: white;
    padding: 0.3rem 0.8rem;
    border-radius: 20px;
    font-size: 0.8rem;
    font-weight: 500;
    box-shadow: 0 0 10px rgba(0, 212, 255, 0.3);
    border: 1px solid rgba(255, 255, 255, 0.2);
}

.project-links {
    display: flex;
    gap: 1rem;
}

.project-link {
    color: #00d4ff;
    text-decoration: none;
    font-weight: 600;
    padding: 0.5rem 1rem;
    border: 2px solid #00d4ff;
    border-radius: 25px;
    transition: all 0.3s ease;
    box-shadow: 0 0 10px rgba(0, 212, 255, 0.2);
}

.project-link:hover {
    background: linear-gradient(45deg, #00d4ff, #ff00ff);
    color: white;
    box-shadow: 0 0 20px rgba(0, 212, 255, 0.4);
    transform: translateY(-2px);
}

/* About Section */
.about-content {
    display: flex;
    align-items: center;
    gap: 3rem;
    max-width: 1000px;
    margin: 0 auto;
}

.about-image {
    flex-shrink: 0;
}

.about-image-container {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background: linear-gradient(45deg, #00d4ff, #ff00ff, #00ff88);
    background-size: 300% 300%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 
        0 0 30px rgba(0, 212, 255, 0.5),
        0 0 60px rgba(255, 0, 255, 0.3),
        0 0 90px rgba(0, 255, 136, 0.2);
    animation: gradientShift 3s ease-in-out infinite;
    position: relative;
    border: 3px solid rgba(255, 255, 255, 0.2);
    padding: 4px;
    overflow: hidden;
}

.about-image-container::before {
    content: '';
    position: absolute;
    top: -3px;
    left: -3px;
    right: -3px;
    bottom: -3px;
    border-radius: 50%;
    background: linear-gradient(45deg, #00d4ff, #ff00ff, #00ff88);
    background-size: 300% 300%;
    z-index: -1;
    animation: gradientShift 3s ease-in-out infinite;
    filter: blur(10px);
    opacity: 0.7;
}

.about-picture {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
    z-index: 2;
    position: relative;
}

/* Content List */
.content-list {
    max-width: 800px;
    margin: 0 auto;
    flex: 1;
}

.content-item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 1.5rem;
    padding: 1.5rem;
    background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
    border-radius: 10px;
    transition: all 0.3s ease;
    border: 1px solid rgba(0, 212, 255, 0.2);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.content-item:hover {
    background: linear-gradient(135deg, #16213e 0%, #1a1a2e 100%);
    transform: translateX(10px);
    border-color: rgba(0, 212, 255, 0.4);
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3), 0 0 20px rgba(0, 212, 255, 0.1);
}

.item-icon {
    color: #00d4ff;
    font-size: 1.5rem;
    margin-right: 1rem;
    margin-top: 0.2rem;
    text-shadow: 0 0 10px rgba(0, 212, 255, 0.5);
}

.item-text {
    color: #ffffff;
    font-size: 1.1rem;
    line-height: 1.6;
}

/* Footer */
.footer {
    background: linear-gradient(135deg, #000000 0%, #1a1a2e 100%);
    color: white;
    padding: 3rem 2rem 1rem;
    border-top: 1px solid rgba(0, 212, 255, 0.2);
}

.footer-content {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

.footer-section h3 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    background: linear-gradient(45deg, #00d4ff, #ff00ff, #00ff88);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0 0 20px rgba(0, 212, 255, 0.3);
}

.footer-section p {
    margin-bottom: 2rem;
    opacity: 0.8;
}

.social-links {
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin-bottom: 2rem;
    flex-wrap: wrap;
}

.social-link {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: white;
    text-decoration: none;
    padding: 0.8rem 1.5rem;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-radius: 25px;
    transition: all 0.3s ease;
}

.social-link:hover {
    background: linear-gradient(45deg, rgba(0, 212, 255, 0.2), rgba(255, 0, 255, 0.2));
    border-color: #00d4ff;
    transform: translateY(-3px);
    box-shadow: 0 5px 15px rgba(0, 212, 255, 0.3);
}

.social-icon {
    font-size: 1.2rem;
}

.footer-bottom {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    padding-top: 1rem;
    opacity: 0.6;
}

/* Animations */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(50px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes float {
    0%, 100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-20px);
    }
}

@keyframes gradientShift {
    0%, 100% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
}

@keyframes backgroundShift {
    0%, 100% {
        transform: translateX(0) translateY(0);
    }
    25% {
        transform: translateX(20px) translateY(-10px);
    }
    50% {
        transform: translateX(-10px) translateY(20px);
    }
    75% {
        transform: translateX(10px) translateY(-5px);
    }
}

@keyframes textGlow {
    0%, 100% {
        text-shadow: 0 0 30px rgba(0, 212, 255, 0.5);
    }
    50% {
        text-shadow: 0 0 40px rgba(0, 212, 255, 0.8), 0 0 60px rgba(255, 0, 255, 0.3);
    }
}

@keyframes textPulse {
    0%, 100% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
}

@keyframes logoGlow {
    0%, 100% {
        text-shadow: 0 0 20px rgba(0, 212, 255, 0.5);
    }
    50% {
        text-shadow: 0 0 30px rgba(0, 212, 255, 0.8), 0 0 50px rgba(255, 0, 255, 0.3);
    }
}

/* Responsive Design */
@media (max-width: 768px) {
    .hero-title {
        font-size: 2.5rem;
    }
    
    .hero-subtitle {
        font-size: 1.2rem;
    }
    
    .nav-links {
        gap: 1rem;
    }
    
    .nav-link {
        padding: 0.3rem 0.8rem;
        font-size: 0.9rem;
    }
    
    .skills-grid {
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
        gap: 1rem;
    }
    
    .projects-grid {
        grid-template-columns: 1fr;
    }
    
    .hero-buttons {
        flex-direction: column;
        align-items: center;
    }
    
    .social-links {
        flex-direction: column;
        align-items: center;
    }
    
    .about-content {
        flex-direction: column;
        text-align: center;
        gap: 2rem;
    }
    
    .about-image-container {
        width: 150px;
        height: 150px;
    }
}

@media (max-width: 480px) {
    .hero-section {
        padding: 1rem;
    }
    
    .hero-title {
        font-size: 2rem;
    }
    
    .section-title {
        font-size: 2rem;
    }
    
    .content-section {
        padding: 3rem 1rem;
    }
    
    .nav-container {
        padding: 0 1rem;
    }
    
    .nav-links {
        gap: 0.5rem;
    }
    
    .nav-link {
        padding: 0.2rem 0.6rem;
        font-size: 0.8rem;
    }
}
